.sidebar {
  text-align: center;
  width: 100vw;
  height: 100vh;
  z-index: 2;
  opacity: 0;
  background-color: #000;
  background-image: linear-gradient(360deg, #edd789e6 0%, #e98283e6 100%), url("banner.a059c1c9.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  align-items: center;
  padding: 0 16px;
  animation: .8s linear forwards bar;
  display: flex;
  position: fixed;
  top: 0;
  left: -100vw;
}

@media screen and (max-width: 768px) {
  .sidebar {
    opacity: 1;
  }
}

.sidebar__header {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 28px;
  display: flex;
}

.sidebar__header img {
  transition: all .2s linear;
}

.sidebar__header img:active {
  transform: scale(1.1);
}

.sidebar__nav-item a {
  color: #fff;
  text-transform: uppercase;
  padding: 24px 0;
  font-size: 13px;
  font-weight: 700;
  line-height: 15px;
  text-decoration: none;
  transition: all .2s linear;
  display: inline-block;
}

.sidebar__nav-item a:hover {
  color: #f4bf87;
}

.sidebar__nav-item a:active {
  color: #2d3033;
  transform: scale(1.2);
}

@media screen and (max-width: 768px) {
  .sidebar.sidebar_checked {
    animation: .8s linear forwards barchecked;
  }
}

@keyframes barchecked {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(100vw);
  }
}

@keyframes bar {
  0% {
    transform: translateX(100vw);
  }

  100% {
    transform: translateX(0);
  }
}

