    line-height: 16px;
  }
}

.tab__button {
  margin: 0 14px;
  transition: all .3s linear;
  transform: perspective(800px)rotateX(0);
}

.tab__button button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

.tab__button button:hover:before {
  filter: invert(30%);
}

.tab__button button:active:before {
  filter: invert();
}

.tab__button :before {
  content: "";
  width: 42px;
  height: 42px;
  background-image: url("arrow.5020a61d.svg");
  background-size: contain;
  transition: all .1s linear;
  display: inline-block;
}

@media screen and (max-width: 1024px) {
  .tab__button :before {
    width: 36px;
    height: 36px;
  }
}

.tab.tab_checked .tab__button {
  transform: perspective(800px)rotateX(180deg);
}

.tab.tab_checked .tab__text {
  max-height: 162px;
  border: 1px solid #c6cbcd;
  border-top: none;
  padding: 18px 24px 18px 22px;
}

